<template>
	<div>
	<div @click="jumpTo('def')"></div>
	<div @click="change()"></div>
	 <ul>
            <li v-for="tab in tabs" >
                {{ tab.text }}
            </li>
        </ul>
	</div>
</template>

<script>
import {vm} from '../../utils/bus.js'
export default {
    data () {
      return {
        highlight:'A',
        item:'11',
        date:'111',
      	tabs: [
                    { text: '' },
                    { text: '' },
                    { text: '' },
                    { text: '' },
                    { text: '' },
                    { text: '' }
                ]
      }
    },
    methods: {
    		jumpTo (page) {
      		this.$router.push({name: page});
    		},
    		change (){
    			this.tabs =  this.tabs.slice(0,2).concat({ text: '' }, this.tabs.slice(2))
    		}
    },
	
}

</script>

<style>
</style>


