<template>
	<div ref="editor">
        <p> <b>wangEditor</b> </p>
    </div>
</template>

<script>
	import E from '../../../static/wangEditor.min.js'
	export default {
      name: 'biji',
      data () {
        return {
          editorContent: ''
        }
      },
      methods: {
        getContent: function () {
            alert(this.editorContent)
        }
      },
      mounted() {
        var editor = new E(this.$refs.editor)
        editor.customConfig.uploadImgShowBase64 = true   //  base64 
    		editor.create()
      }
    }
</script>

<style>
	
</style>


